import firebase from "gatsby-plugin-firebase"
import React, { useCallback, useState, useContext } from "react"
import { useForm } from "react-hook-form"

import { PrimaryButton } from "./button"
import { Dialog } from "./dialog"
import { Input } from "./input"
import { Error, Similink, Title } from "./primitives"
import { View } from "./view"
import { NotifyContext } from "../contexts/notify"
import { DIALOG_CLOSED_REASON } from "../constants"
import { FormattedMessage } from "react-intl"

const SignupTab = ({ onSignup, switchTab }) => {
  const {
    handleSubmit,
    register,
    watch,
    setError,
    errors,
    formState,
  } = useForm()

  const password = watch("password")

  const onSubmit = useCallback(
    async ({ email, username, password }) => {
      try {
        const { user } = await firebase
          .auth()
          .createUserWithEmailAndPassword(email, password)

        const userMetadata = { username, signupDate: Date.now() }

        await firebase
          .firestore()
          .collection("users")
          .doc(user.uid)
          .set(userMetadata)

        onSignup({
          uid: user.uid,
          ...userMetadata,
          firebaseUser: user,
        })
      } catch (error) {
        switch (error.code) {
          case "auth/weak-password":
            setError(
              "password",
              "weakPassword",
              <FormattedMessage id="Password is too weak" />
            )
            break
          case "auth/email-already-in-use":
            setError(
              "email",
              "alreadyExists",
              <FormattedMessage id="Email address already in use" />
            )
            break
          case "auth/invalid-email":
            setError(
              "email",
              "invalid",
              <FormattedMessage id="Invalid email address" />
            )
            break
          default:
            return
        }
      }
    },
    [onSignup, setError]
  )

  return (
    <View
      as="form"
      onSubmit={handleSubmit(onSubmit)}
      css={{ flex: "1", gap: 3 }}
    >
      <View css={{ gap: 3, overflow: "auto", flex: "1" }}>
        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Email address" />
          </label>
          <Input
            type="email"
            name="email"
            ref={register({
              required: <FormattedMessage id="Email address is required" />,
            })}
          ></Input>
          {errors.email && <Error>{errors.email.message}</Error>}
        </View>

        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Username" />
          </label>
          <Input
            name="username"
            ref={register({
              required: <FormattedMessage id="Username is required" />,
            })}
          ></Input>
          {errors.username && <Error>{errors.username.message}</Error>}
        </View>

        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Password" />
          </label>
          <Input
            type="password"
            name="password"
            ref={register({
              required: <FormattedMessage id="Password is required" />,
            })}
          ></Input>
          {errors.password && <Error>{errors.password.message}</Error>}
        </View>

        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Confirm password" />
          </label>
          <Input
            type="password"
            name="passwordConfirm"
            ref={register({
              required: "Password is required",
              validate: (passwordConfirm) =>
                passwordConfirm === password || (
                  <FormattedMessage id="Passwords must match" />
                ),
            })}
          ></Input>
          {errors.passwordConfirm && (
            <Error>{errors.passwordConfirm.message}</Error>
          )}
        </View>
      </View>

      <PrimaryButton disabled={formState.isSubmitting} type="submit">
        <FormattedMessage id="Sign me up !" />
      </PrimaryButton>

      <View
        css={{
          alignItems: "center",
        }}
      >
        <Similink onClick={switchTab}>
          <FormattedMessage id="I already have an account" />
        </Similink>
      </View>
    </View>
  )
}

const SignInTab = ({ onSignin, switchTab }) => {
  const { handleSubmit, register, setError, errors, formState } = useForm()

  const onSubmit = useCallback(
    async ({ email, password }) => {
      try {
        const { user } = await firebase
          .auth()
          .signInWithEmailAndPassword(email, password)

        const snapshot = await firebase
          .firestore()
          .collection("users")
          .doc(user.uid)
          .get()

        const userMetadata = snapshot.data()

        onSignin({
          uid: user.uid,
          ...userMetadata,
          firebaseUser: user,
        })
      } catch (error) {
        switch (error.code) {
          case "auth/invalid-email":
          case "auth/user-not-found":
          case "auth/user-disabled":
            setError(
              "email",
              "invalid",
              <FormattedMessage id="Unknown email address" />
            )
            break
          case "auth/wrong-password":
            setError(
              "password",
              "mismatch",
              <FormattedMessage id="Incorrect password" />
            )
            break

          default:
            return
        }
      }
    },
    [onSignin, setError]
  )

  return (
    <View
      as="form"
      onSubmit={handleSubmit(onSubmit)}
      css={{ flex: "1", gap: 3 }}
    >
      <View css={{ gap: 3, overflow: "auto", flex: "1" }}>
        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Email address" />
          </label>
          <Input
            type="email"
            name="email"
            ref={register({
              required: <FormattedMessage id="Email address is required" />,
            })}
          ></Input>
          {errors.email && <Error>{errors.email.message}</Error>}
        </View>

        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Password" />
          </label>
          <Input
            type="password"
            name="password"
            ref={register({
              required: <FormattedMessage id="Password is required" />,
            })}
          ></Input>
          {errors.password && <Error>{errors.password.message}</Error>}
        </View>
      </View>

      <PrimaryButton type="submit" disabled={formState.isSubmitting}>
        <FormattedMessage id="Sign me in !" />
      </PrimaryButton>

      <View
        css={{
          alignItems: "center",
        }}
      >
        <Similink onClick={switchTab}>
          <FormattedMessage id="I don't have an account" />
        </Similink>
      </View>
    </View>
  )
}

export const LoginDialog = ({ deferred }) => {
  const notify = useContext(NotifyContext)

  const [tab, setTab] = useState("signup")

  const handleLogin = (currentUser) => {
    deferred.resolve(currentUser)
    notify(
      <FormattedMessage
        id="Logged in as {username}"
        values={{ username: currentUser.username }}
      />
    )
  }

  return (
    <Dialog
      title={
        tab === "signup" ? (
          <Title>
            <FormattedMessage id="Create your account" />
          </Title>
        ) : (
          <Title>
            <FormattedMessage id="Connect to your account" />
          </Title>
        )
      }
      onClose={() => deferred.reject(DIALOG_CLOSED_REASON)}
      css={{ gap: 3 }}
    >
      {tab === "signup" ? (
        <SignupTab
          onSignup={handleLogin}
          switchTab={() => setTab("signin")}
        ></SignupTab>
      ) : (
        <SignInTab
          onSignin={handleLogin}
          switchTab={() => setTab("signup")}
        ></SignInTab>
      )}
    </Dialog>
  )
}
