import React, {
  useState,
  useCallback,
  useEffect,
  useMemo,
  createContext,
} from "react"

import clapSound from "../sounds/clap.wav"
import buttonSound from "../sounds/button.wav"
import cardSound from "../sounds/card.wav"
import tangramSound from "../sounds/pop.wav"
import UIfx from "../utils/uifx"

export const SoundContext = createContext([true, () => {}])

const SOUND_MAP = {
  clap: new UIfx(clapSound),
  button: new UIfx(buttonSound),
  toggle: new UIfx(buttonSound),
  card: new UIfx(cardSound),
  tangram: new UIfx(tangramSound),
}

export const SoundProvider = ({ children }) => {
  const [soundEnabled, setSoundEnabled] = useState(true)

  useEffect(() => {
    setSoundEnabled(
      window.localStorage.getItem("sound") === "off" ? false : true
    )
  }, [])

  const toggleSound = useCallback(() => {
    const newSoundsEnabled = !soundEnabled
    setSoundEnabled(newSoundsEnabled)
    window.localStorage.setItem("sound", newSoundsEnabled ? "on" : "off")
  }, [soundEnabled])

  const contextValue = useMemo(
    () => ({
      soundEnabled,
      toggleSound,
      play: (soundId, { volume, forceSoundEnabled } = {}) => {
        if (forceSoundEnabled !== undefined) {
          if (forceSoundEnabled) {
            SOUND_MAP[soundId].play(volume)
          }
        } else if (soundEnabled) {
          SOUND_MAP[soundId].play(volume)
        }
      },
    }),
    [soundEnabled, toggleSound]
  )

  return (
    <SoundContext.Provider value={contextValue}>
      {children}
    </SoundContext.Provider>
  )
}
