import firebase from "gatsby-plugin-firebase"
import React, { useContext, useState, useEffect, useMemo } from "react"
import { CATEGORIES, DIGITS, LETTERS, DIALOG_CLOSED_REASON } from "../constants"

import { NotifyContext } from "../contexts/notify"
import { UserContext } from "../contexts/user"
import { getRandomEmoji } from "../utils/getRandomEmoji"
import { PrimaryButton } from "./button"
import { Card } from "./card"
import { Dialog } from "./dialog"
import { Input } from "./input"
import { Similink, Title, Error, Hint } from "./primitives"
import { Text } from "./text"
import { View } from "./view"
import { recomputePathData } from "../utils/recomputePathData"
import { useForm } from "react-hook-form"
import { FormattedMessage, useIntl } from "react-intl"

const ReadTangramDialog = ({ tangram, deferred }) => {
  return (
    <Dialog
      onClose={() => deferred.reject(DIALOG_CLOSED_REASON)}
      css={{ gap: 3, minWidth: "268px" }}
    >
      <View css={{ gap: 3, overflow: "auto", flex: "1", alignItems: "center" }}>
        <Card selected tangram={tangram}></Card>
        <Text>
          <FormattedMessage
            id="Earned {claps} 👏"
            values={{
              claps: tangram.claps || 0,
            }}
          />
        </Text>
      </View>
    </Dialog>
  )
}

const SaveTangramDialog = ({ tangram, deferred }) => {
  const intl = useIntl()
  const notify = useContext(NotifyContext)
  const { currentUser } = useContext(UserContext)
  const [tangramCopy, setTangramCopy] = useState({ ...tangram })

  const isCreation = currentUser && !tangram.id

  const defaultValues = useMemo(
    () => ({
      path: tangram.path,
      category: tangram.category,
      emoji:
        tangram.category !== "digits" && tangram.category !== "letters"
          ? tangram.emoji
          : getRandomEmoji(),
      letterIndex:
        tangram.category === "letters"
          ? LETTERS.indexOf(tangram.emoji)
          : undefined,
      digitIndex:
        tangram.category === "digits"
          ? DIGITS.indexOf(tangram.emoji)
          : undefined,
    }),
    [tangram]
  )

  const {
    handleSubmit,
    register,
    watch,
    errors,
    setError,
    clearError,
  } = useForm({
    defaultValues,
  })

  const { path, category, emoji, letterIndex, digitIndex } = watch()

  useEffect(() => {
    try {
      const pathData = recomputePathData(path)
      setTangramCopy((prevTangramCopy) => ({
        ...prevTangramCopy,
        ...pathData,
      }))
      clearError("path")
    } catch (error) {
      setError("path", "invalid", <FormattedMessage id="Invalid path" />)
    }
  }, [path, setError, clearError])

  useEffect(() => {
    const computedEmoji =
      category === "letters"
        ? LETTERS[letterIndex]
        : category === "digits"
        ? DIGITS[digitIndex]
        : emoji
        ? emoji
        : getRandomEmoji()

    setTangramCopy((prevTangramCopy) => ({
      ...prevTangramCopy,
      emoji: computedEmoji,
    }))
  }, [category, emoji, letterIndex, digitIndex])

  useEffect(() => {
    setTangramCopy((prevTangramCopy) => ({
      ...prevTangramCopy,
      category,
    }))
  }, [category])

  const handleDelete = async () => {
    if (
      window.confirm(
        intl.formatMessage({
          id: "Are you sure you want to delete this tangram ?",
        })
      )
    ) {
      await firebase.firestore().collection("tangrams").doc(tangram.id).delete()
      notify(<FormattedMessage id="Tangram sucessfuly deleted" />)
      deferred.resolve()
    }
  }

  const onSubmit = async () => {
    if (isCreation) {
      await firebase
        .firestore()
        .collection("tangrams")
        .add({
          ...tangramCopy,
          uid: currentUser.uid,
          approved: false,
        })
      notify(<FormattedMessage id="Tangram submitted for review" />)
    } else {
      await firebase
        .firestore()
        .collection("tangrams")
        .doc(tangram.id)
        .update(tangramCopy)
      notify(<FormattedMessage id="Tangram successfuly modified" />)
    }

    deferred.resolve()
  }

  return (
    <Dialog
      title={
        <Title>
          {isCreation ? (
            <FormattedMessage id="Submit your tangram" />
          ) : (
            <FormattedMessage id="Edit your tangram" />
          )}
        </Title>
      }
      onClose={() => deferred.reject(DIALOG_CLOSED_REASON)}
      as="form"
      onSubmit={handleSubmit(onSubmit)}
      css={{ gap: 3, minWidth: "268px" }}
    >
      <View css={{ gap: 3, overflow: "auto", flex: "1" }}>
        <Card
          selected
          completed
          tangram={tangramCopy}
          css={{ alignSelf: "center" }}
        ></Card>

        {tangram.approved && (
          <Text css={{ alignSelf: "center" }}>
            <FormattedMessage
              id="Earned {claps} 👏"
              values={{
                claps: tangram.claps || 0,
              }}
            />
          </Text>
        )}

        {currentUser && currentUser.isAdmin && (
          <>
            <View css={{ gap: 2 }}>
              <label>
                <FormattedMessage id="Path" />
              </label>
              <Input as="textarea" name="path" ref={register}></Input>
              {errors.path && <Error>{errors.path.message}</Error>}
            </View>
          </>
        )}

        <View css={{ gap: 2 }}>
          <label>
            <FormattedMessage id="Category" />
          </label>
          <Input as="select" name="category" ref={register}>
            {CATEGORIES.map((category) => (
              <option key={category} value={category}>
                <FormattedMessage id={category} />
              </option>
            ))}
          </Input>
        </View>

        {category === "digits" ? (
          <View css={{ gap: 2 }}>
            <label>
              <FormattedMessage id="Victory emoji" />
            </label>
            <Input as="select" name="digitIndex" ref={register}>
              {DIGITS.map((digitEmoji, index) => (
                <option key={index} value={index}>
                  {digitEmoji}
                </option>
              ))}
            </Input>
          </View>
        ) : category === "letters" ? (
          <View css={{ gap: 2 }}>
            <label>
              <FormattedMessage id="Victory emoji" />
            </label>
            <Input as="select" name="letterIndex" ref={register}>
              {LETTERS.map((letterEmoji, index) => (
                <option key={index} value={index}>
                  {letterEmoji}
                </option>
              ))}
            </Input>
          </View>
        ) : (
          <View css={{ gap: 2 }}>
            <label>
              <FormattedMessage id="Victory emoji" />
            </label>
            <Input name="emoji" ref={register} />
            <Hint>
              <FormattedMessage id="Leave empty for random emoji" />
            </Hint>
          </View>
        )}
      </View>
      <PrimaryButton type="submit">
        {isCreation ? (
          <FormattedMessage id="Submit !" />
        ) : (
          <FormattedMessage id="Edit !" />
        )}
      </PrimaryButton>
      {!isCreation && (
        <View
          css={{
            alignItems: "center",
          }}
        >
          <Similink onClick={handleDelete}>
            <FormattedMessage id="Or delete tangram" />
          </Similink>
        </View>
      )}
    </Dialog>
  )
}

export const TangramDialog = ({ tangram, deferred }) => {
  const { currentUser } = useContext(UserContext)
  const isEdit =
    currentUser &&
    (currentUser.isAdmin || !tangram.id || tangram.uid === currentUser.uid)

  return isEdit ? (
    <SaveTangramDialog tangram={tangram} deferred={deferred} />
  ) : (
    <ReadTangramDialog tangram={tangram} deferred={deferred} />
  )
}
